import React, {FC} from 'react';
import bgMobileProducts from '../../assets/img/products/bg-mobile-products.svg';
import {data} from "./data";

interface IProduct {
    setIsOpenModal: () => void
}

export const Product: FC<IProduct> = ({setIsOpenModal}) => {
    return (
        <div className="product">
            <div className="container">
                <img src={bgMobileProducts} alt="" className="product-bg-mobile"/>
                <img src={bgMobileProducts} alt="" className="product-bg-mobile" style={{bottom: -88, top: 'unset'}}/>

                {
                    data.map(({category, products}, i) => (
                        <div className="product-content" key={i}>
                            <h2>{category}</h2>
                            <div className="product-content_items">
                                {
                                    products.map(({src, title, price}, j) => (
                                        <div className="product-content_items--item" key={j}>
                                            <img src={src} alt=""/>
                                            <h3>{title}</h3>
                                            <p>{price}</p>
                                            <button onClick={setIsOpenModal}>Подробнее</button>
                                        </div>
                                    ))
                                }
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    );
};

