import React, {FC} from 'react';
import src from '../../assets/img/products/product-7-big.png';

interface IProductUs {
    setIsOpenModal: () => void
}

export const ProductUs: FC<IProductUs> = ({setIsOpenModal}) => {
    return (
        <div className="product-us">
            <div className="product-us-bg" />

            <div className="container">
                <div className="product-us-content">
                    <div className="product-us-content_desc">
                        <h2>ЗАГОЛОВОК</h2>
                        <p>
                            Барбершоп  для тех, кто ценит хороший сервис, качество стрижки и серьезное отношение. Для всех, кому важен опрятный внешний вид бороды и волос, кто всегда хочет быть, как «с иголочки», у нас есть все необходимое для осуществления подобных желаний.
                        </p>
                    </div>

                    <div className="product-us-content_product">
                        <img src={src} alt=""/>
                        <button onClick={setIsOpenModal}>О товаре</button>
                    </div>
                </div>
            </div>
        </div>
    );
};

