import React, {useState} from 'react';

export const ContactUs = () => {
    const [checked, setChecked] = useState(true);

    return (
        <div className="contact-us">
            <div className="product-us-bg"/>
            <div className="container">
                <div className="contact-us_content">
                    <div className="contact-us_content-desc">
                        <h2>
                            У ВАС ОСТАЛИСЬ ВОПРОСЫ?
                        </h2>

                        <p>ЗАПОЛНИТЕ ПОЛЯ И МЫ ОТВЕТИМ ВАМ!</p>
                    </div>

                    <div className="contact-us_content-form">
                        <form onSubmit={() => {}}>
                            <h4>Оставте заявку</h4>
                            <p>Мы ответим вам!</p>

                            <input type="text" placeholder={'Ваше Имя'}/>
                            <input type="text" placeholder={'E-mail'}/>
                            <input type="text" placeholder={'Ваш телефон'}/>
                            <button>Отправить заявку</button>
                            <div className="contact-us_content-form-check" onClick={() => setChecked(!checked)}>
                                <div>
                                    <input type="checkbox" checked={checked}/>
                                    <span className="checkmark"/>
                                </div>
                                <span>Согласен (на) с условиями Пользовательского соглашения</span>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
};

