import React, {useEffect, useState} from 'react';
import './App.css';
import {Header} from "./components/Header/Header";
import {Product} from "./components/Product/Product";
import {ProductUs} from "./components/ProductUs/ProductUs";
import {AboutUs} from "./components/AboutUs/AboutUs";
import {Reviews} from "./components/Reviews/Reviews";
import {ContactUs} from "./components/ContactUs/ContactUs";
import {Footer} from "./components/Footer/Footer";
import {ModalProduct} from "./components/ModalProduct/ModalProduct";
import arrowTop from './assets/img/arrow-top.svg';

export const App = () => {
    const [isOpenModal, setIsOpenModal] = useState(false);
    const [type, setType] = useState('product');
    const [isShow, setIsShow] = useState(false);
    const setIsOpenModalHandler = (value: boolean) => {
        setType("form");
        setIsOpenModal(value);
    };

    useEffect(() => {
        window.onscroll = () => {
            if (window.pageYOffset >= 200) {
                setIsShow(true);
            } else {
                setIsShow(false);
            }
        }
    }, []);

    return (
        <div className="landing">
            {
                isShow &&
                <a href="#">
                    <img src={arrowTop}
                         className="landing-scroll-button"
                    />
                </a>
            }
            <ModalProduct isOpen={isOpenModal}
                          setIsOpenModal={() =>{
                              setIsOpenModal(false);
                              setType('product')
                          }}
                          type={type}
                          setType={(v) => setType(v)}
            />
            <Header setIsOpenModal={setIsOpenModalHandler}/>
            <Product setIsOpenModal={() => setIsOpenModal(true)}/>
            <ProductUs setIsOpenModal={() => setIsOpenModal(true)}/>
            <AboutUs/>
            <Reviews/>
            <ContactUs/>
            <Footer/>
        </div>
    );
};

