import React, {FC} from 'react';
import logo from '../../assets/img/Logo.svg';
import mobileBurger from '../../assets/img/mobile-burger.svg';
import instagram from '../../assets/img/instagram.svg';
import facebook from '../../assets/img/facebook.svg';

interface IMobileMenu {
    setIsOpen: (v: boolean) => void
    isOpen: boolean
}

export const MobileMenu: FC<IMobileMenu> = ({isOpen, setIsOpen}) => {

    return (
        <div className='menu-mobile' style={{display: isOpen ? 'block' : 'none'}}>
            <div className="menu-mobile_header">
                <img src={logo} alt="" className='menu-logo'/>
                <img src={mobileBurger} alt="" onClick={() => setIsOpen(false)}/>
            </div>

            <div className="menu-mobile_desc">
                <a href="#about" onClick={() => {
                    setIsOpen(false);
                }}>О нас</a>
                <a href="#review" onClick={() => {
                    setIsOpen(false);
                }}>Отзывы</a>
                <div>
                    <a href="">
                        <img src={instagram} alt=""/>
                    </a>
                    <a href="">
                        <img src={facebook} alt=""/>
                    </a>
                </div>
            </div>
        </div>
    );
};

