import React, {FC, useEffect, useRef, useState} from 'react';
import useOnClickOutside from "../../hooks/useOnClickOutside";
import product from "../../assets/img/products/product2.png"

interface IModalProduct {
    isOpen: boolean
    type: string
    setIsOpenModal: () => void
    setType: (v: string) => void
}

export const ModalProduct: FC<IModalProduct> = ({isOpen, setIsOpenModal, type, setType}) => {
    const [hasClickedOutside, setHasClickedOutside] = useState(false);
    const ref = useRef(null);
    useOnClickOutside(ref, () => setHasClickedOutside(true));


    useEffect(() => {
        if (hasClickedOutside) {
            setIsOpenModal();
        }
    }, [hasClickedOutside]);

    return (
        <div className="modal-abstract"
             style={{display: isOpen ? 'block' : 'none'}}
             onClick={() => {
            if (hasClickedOutside) {
                setIsOpenModal();
            }
        }}>
            {type === 'product' && <div className="modal-product" ref={ref}>
                <div className="modal-abstract-close" onClick={() => {
                    if (!hasClickedOutside) {
                        setIsOpenModal();
                    }
                }}>X</div>
                <div className="modal-line" style={{top: 40}}/>
                <div className="modal-line" style={{bottom: 30}}/>
                <div className="modal-product_item">
                    <div className="modal-product_item-img">
                        <img src={product} alt=""/>
                    </div>

                    <div className="modal-product_item-desc">
                        <h2>Ножницы Проффесионала</h2>
                        <h3>Цена: 980р</h3>
                        <p>О товаре</p>
                        <span>Повседневная практика показывает, что консультация с профессионалами из IT обеспечивает широкому кругу специалистов участие в формировании направлений прогрессивного развития. С другой стороны выбранный нами инновационный путь требует определения и уточнения новых предложений.</span>
                    </div>
                </div>

                <button onClick={() => setType('form')}>Купить</button>
            </div>}

            {type === 'form' && <div className="modal-form" ref={ref}>
                <div className="modal-abstract-close" onClick={() => {
                    if (!hasClickedOutside) {
                        setIsOpenModal();
                    }
                }}>X</div>
                <div className="modal-form-bg" />
                <div className="contact-us_content-form">
                    <form onSubmit={() => {}}>
                        <h4>Оставте заявку</h4>
                        <p>Мы ответим вам!</p>

                        <input type="text" placeholder={'Ваше Имя'}/>
                        <input type="text" placeholder={'E-mail'}/>
                        <input type="text" placeholder={'Ваш телефон'}/>
                        <button onClick={() => setType('finish')}>Отправить заявку</button>
                        <div className="contact-us_content-form-check">
                            <div>
                                <input type="checkbox"
                                       //checked="checked"
                                />
                                <span className="checkmark"/>
                            </div>
                            <span>Согласен (на) с условиями Пользовательского соглашения</span>
                        </div>
                    </form>
                </div>
            </div>}

            {type === 'finish' && <div className="modal-finish" ref={ref}>
                <div className="modal-abstract-close" onClick={() => {
                    if (!hasClickedOutside) {
                        setIsOpenModal();
                    }
                }}>X</div>
                <div className="modal-line" style={{top: 30}}/>
                <div className="modal-line" style={{bottom: 27}}/>
                <div className="modal-form-bg" />
                <div className="modal-finish_content">
                    <h2>
                        СПАСИБО ЗА ЗАЯВКУ <br/>
                        МЫ ВАМ ПОЗВОНИМ В ТЕЧЕНИИ НЕСКОЛЬКИХ МИНУТ
                    </h2>

                    <div className="modal-finish_content-button">
                        <p onClick={() => {
                            if (!hasClickedOutside) {
                                console.log('hello')
                                setIsOpenModal();
                            }
                        }}>Перейти на главную</p>
                        <img src="/img/arrow-modal.svg" alt="" onClick={() => {
                            if (!hasClickedOutside) {
                                setIsOpenModal();
                            }
                        }}/>
                    </div>
                </div>
            </div>}
        </div>
    );
};

