import React, {FC, useState} from 'react';
import {Menu} from "../Menu/Menu";
import {MobileMenu} from "../MenuMobile/MenuMobile";
import mobileBurger from '../../assets/img/mobile-burger.svg';

export interface IHeader {
    setIsOpenModal: (value: boolean) => void
}

export const Header:FC<IHeader> = ({setIsOpenModal}) => {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <div className="header" id={'header'}>
            <MobileMenu isOpen={isOpen} setIsOpen={(value: boolean) => setIsOpen(value)}/>
            <div className="header-burger"
                 style={{display: isOpen ? 'none' : 'block'}}
                 onClick={() => setIsOpen(true)}
            >
                <img src={mobileBurger} alt="" />
            </div>
            <div className="header-bg-black" />
            <div className="container">
                <div className="header-content">
                    <Menu />

                    <h2>Добро пожаловать!</h2>
                    <h1>Твой новый <br/> безупречный стиль</h1>
                    <button onClick={() => setIsOpenModal(true)}>Оставить заявку</button>
                </div>
            </div>
        </div>
    );
};

