import React from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import photo from '../../assets/img/photo.png';

export const Reviews = () => {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };

    return (
        <div className="reviews" id={'reviews'}>
            <div className="container">
                <div className="reviews-content">
                    <h2>
                        Отзывы Клиентов
                    </h2>

                    <Slider {...settings}>
                        <div className="slide">
                            <img src={photo} alt=""/>
                            <div className="slide-desc">
                                <h4>Александр Зайчиков</h4>
                                <p>Дорогие друзья, сходил я на этот ваш барбершоп. Думал обычное парикмахерское, но был не прав. Мне все понравилось.</p>
                            </div>
                        </div>

                        <div className="slide">
                            <img src={photo} alt=""/>
                            <div className="slide-desc">
                                <h4>Александр Зайчиков</h4>
                                <p>Дорогие друзья, сходил я на этот ваш барбершоп. Думал обычное парикмахерское, но был не прав. Мне все понравилось.</p>
                            </div>
                        </div>
                    </Slider>
                </div>
            </div>
        </div>
    );
};

